/**
 * Layout component that queries for data
 * with Gatsby's useStaticQuery component
 *
 * See: https://www.gatsbyjs.org/docs/use-static-query/
 */

import React, { useState } from "react"
import PropTypes from "prop-types"
import { useStaticQuery, graphql, Link } from "gatsby"
import SwipeableDrawer from '@material-ui/core/SwipeableDrawer'
import Header from "./header"
import "./layout.css"
import 'bootstrap/dist/css/bootstrap.min.css';
import ArrowBackIosIcon from '@material-ui/icons/ArrowBackIos';
import ContactForm from "./contactForm"
import InstagramIcon from '@material-ui/icons/Instagram';
import FacebookIcon from '@material-ui/icons/Facebook';

const Layout = ({ children }) => {
  const iOS = process.browser && /iPad|iPhone|iPod/.test(navigator.userAgent);
  const [contactOpen, setContactOpen] = useState(false);
  const data = useStaticQuery(graphql`
    query SiteTitleQuery {
      site {
        siteMetadata {
          title
        }
      }
    }
  `)

  return (
    <>
      <div class="main-grid">
        <Header siteTitle={data.site.siteMetadata.title} />

        <main>{children}</main>
        <footer class="vg-footer">
          <div class="footer-container">
            <section  >
              <h4>Vra tjnster:</h4>
              <nav>
                <ul >
                  <li>
                    <Link  to="/golvlaggning">Golvlggning</Link>
                  </li>
                  <li>
                    <Link  to="/golvslipning">Golvslipning</Link>
                  </li>
                  <li>
                    <Link  to="/mattlaggning">Mattlggning</Link>
                  </li>
                  <li>
                    <Link  to="/golvavjamning">Golvavjmning</Link>
                  </li>
                  <li>
                    <Link  to="/vattenskador">Vattenskador</Link>
                  </li>
                  <li>
                    <Link  to="/trapprenovering">Trapprenovering</Link>
                  </li>
                </ul>


              </nav>
            </section>
            <section class="warranty-section">
              <h4 >Garanti och frskringar:</h4><br />
              <p >Vi erbjuder tv till fem rs garanti beroende p vilket typ av golv du lgger.</p>
              <p >Fr er och vr skerhet r vi allriskfrskrade hos Folksam.</p>
            </section>
            <section >
              <address>
                <h4>Kontakt</h4>
                <p >Fretagsnummer:<br />
                  <a  href="tel:08-999017">08-999017</a>
                </p>

                <p >Direktnummer:<br />
                  <a href="tel:0732199908">0732199908</a><br />
                  <a  href="tel:0732199908">0732199909</a>
                </p>

                <p >Epost:<br />
                  <a  href="mailto:kontakt@victoriagolv.se">info@victoriagolv.se</a><br />
                </p>
                <div class="icon-container">
                  <a  href="https://www.instagram.com/victoriagolvab/?hl=sv"><InstagramIcon /></a>
                  <FacebookIcon />
                </div>
              </address>
            </section>
          </div>

          <p >Victoria Golv  {new Date().getFullYear()}</p>


        </footer>
      </div>

      {!contactOpen &&
        <aside class="d-none d-lg-flex contact-tab" onClick={() => setContactOpen(true)}>
          <ArrowBackIosIcon  />
          <p >Kontakta<br />oss</p>
        </aside>
      }
      <SwipeableDrawer disableBackdropTransition={!iOS} disableDiscovery={iOS} open={contactOpen} onClose={() => setContactOpen(false)} anchor={"right"}>

        <ContactForm />
      </SwipeableDrawer>
    </>
  )
}

Layout.propTypes = {
  children: PropTypes.node.isRequired,
}

export default Layout
