import React, { useState } from "react"
import FormControl from '@material-ui/core/FormControl';
import Input from '@material-ui/core/Input';
import InputLabel from '@material-ui/core/InputLabel';
import Select from '@material-ui/core/Select';
import MenuItem from '@material-ui/core/MenuItem';
import Button from '@material-ui/core/Button';
import { TextareaAutosize } from "@material-ui/core";


const ContactForm = () => {

    const [service, setService] = useState(null)
    const [message, setMessage] = useState("")
    return (
        <div style={{ height: "100%", minWidth: "30vw", maxWidth: "50vw", display: "flex", justifyContent: "center", background: "white" }}>
            <div style={{ display: "flex", alignItems: "center", flexDirection: "column" }}>
                <h2 style={{ textAlign: "center" }}>Kontakta oss</h2>
                <form style={{ display: "flex", alignItems: "center", flexDirection: "column" }} action="https://getform.io/f/9ab82b0b-c75d-4d5b-8382-2d3e1a6aef68" method="POST">
                    <FormControl >

                        <InputLabel htmlFor="name-input">Ditt namn</InputLabel>
                        <Input type="text" name="name" />



                    </FormControl>
                    <FormControl >

                        <InputLabel htmlFor="email-input">Din email</InputLabel>
                        <Input type="email"  name="email" />



                    </FormControl>
                    <FormControl >

                        <InputLabel htmlFor="tel-input">Ditt telefonnummer</InputLabel>
                        <Input type="tel"  name="tel" />



                    </FormControl>

                    <FormControl style={{ width: "100%" }}>
                        <InputLabel id="services-select-label">Vlj tjnst</InputLabel>
                        <Select
                            labelId="services-select-label"
                            id="services-select"
                            onChange={(event) => { setService(event.target.value)}}
                        >
                            <MenuItem value={"Golvlggning"}>Golvlggning</MenuItem>
                            <MenuItem value={"Golvslipning"}>Golvslipning</MenuItem>
                            <MenuItem value={"Mattlggning"}>Mattlggning</MenuItem>
                            <MenuItem value={"Golvavjmning"}>Golvavjmning</MenuItem>
                            <MenuItem value={"Vattenskador"}>Vattenskador</MenuItem>
                            <MenuItem value={"Trapprenovering"}>Trapprenovering</MenuItem>

                        </Select>
                    </FormControl>
                    <FormControl style={{ width: "100%", marginTop: "1rem" }}>
                        <TextareaAutosize onChange={(event) => { setMessage(event.target.value) }} aria-label="beskriv rende" rowsMin={5} placeholder="Beskriv ditt rende" />
                    </FormControl>
                    <FormControl>
                        <input hidden  type="text" name="message" value={`Intresserad av tjnsten: ${service} \n Meddelande:\n ${message}`} />
                    </FormControl>
                    <Button type="submit" style={{ marginTop: "1rem", background:"lightgreen" }} variant="contained">Skicka</Button>
                </form>
            </div>
        </div>
    );
}

export default ContactForm;