import React, { useState } from "react"
import PropTypes from "prop-types"
import { Link } from "gatsby";
import { NavDropdown } from "react-bootstrap";
import MenuIcon from '@material-ui/icons/Menu';
import { IconButton, List, ListItem, ListItemText, SwipeableDrawer } from "@material-ui/core";

const Nav = ({ logo }) => {
    const iOS = process.browser && /iPad|iPhone|iPod/.test(navigator.userAgent);
    const [ytbehandlingVisible, setYtbehandlingVisible] = useState(false);
    const [ovrigaVisible, setOvrigaVisible] = useState(false);
    const [openNav, setOpenNav] = useState(false);
    return (
        <>
            <nav class="d-lg-none d-md-none d-xl-none mobile-nav">
                <Link to="/"><img height="80px" src={logo} alt="Logga" /></Link>
                <IconButton onClick={() => setOpenNav(!openNav)}>
                    <MenuIcon />
                </IconButton>
            </nav>
            <nav class="desktop-nav d-none d-md-flex">

                <ul style={{ width: "100%", display: "flex", justifyContent: "space-evenly  ", listStyle: "none" }}>
                    <li>
                        <Link to="/">Hem</Link>
                    </li>
                    {/* <li>
                    <Link to="/golvlaggning">Golvlggning
                   
                    </Link>
                </li>
                <li onMouseLeave={()=>setYtbehandlingVisible(false)}
                    onMouseEnter={()=>setYtbehandlingVisible(true)}>
                    <div class="dropdown-nav-link" >
                    
                    <div class="" className={`dopdown-nav ${ytbehandlingVisible ? "" : "hidden-nav"}`}>
                        <Link to="/ytbehandling">Ytbehandling</Link>
                    </div>
                    </div>

                </li> */}
                    <li onMouseLeave={() => setOvrigaVisible(false)}
                        onMouseEnter={() => setOvrigaVisible(true)}>
                        <div class="dropdown-nav-link" >
                            <a>Tjnster</a>

                            <div className={`dopdown-nav ${ovrigaVisible ? "" : "hidden-nav"}`}>

                                <Link to="/golvlaggning" >Golvlggning</Link>
                                <div onMouseLeave={() => setYtbehandlingVisible(false)}
                                    onMouseEnter={() => setYtbehandlingVisible(true)}>
                                    <Link to="/golvslipning" >Golvslipning</Link>
                                    <div className={`dopdown-nav ytbehandling-link ${ytbehandlingVisible ? "" : "hidden-nav"}`}>
                                        <Link to="/ytbehandling">Ytbehandling</Link>
                                    </div>
                                </div>
                                <Link to="/mattlaggning">Mattlggning</Link>
                                <Link to="/golvavjamning">Golvavjmning</Link>
                                <Link to="/vattenskador">Vattenskador</Link>
                                <Link to="/trapprenovering">Trapprenovering</Link>
                            </div>
                        </div>

                    </li>

                    <li>
                        <Link to="/offertforfragan">Offertfrfrgan</Link>
                    </li>
                    <li>
                        <Link to="/om-oss">Om oss</Link>
                    </li>
                </ul>






            </nav>
            <SwipeableDrawer anchor={"top"} class="d-lg-none d-md-none d-xl-none" disableBackdropTransition={!iOS} disableDiscovery={iOS} open={openNav} >
                <List>
                    <Link to="/">
                        <ListItem button key={"Hem"}>
                            Hem
                    </ListItem>
                    </Link>
                    <Link to="/golvlaggning" >
                    <ListItem button key={"golvlaggning"}>


                        Golvlggning

                    </ListItem>
                    </Link>

                    <Link to="/golvslipning">
                    <ListItem button key={"golvslipning"}>

                        Golvslipning
                    </ListItem>
                    </Link>

                    <Link to="/ytbehandling" >
                    <ListItem button key={"ytbehandling"}>


                        Ytbehandling

                    </ListItem>
                    </Link>

                    <Link to="/mattlaggning" >
                    <ListItem button key={"mattlaggning"}>


                        Mattlggning

                    </ListItem>
                    </Link>
                    <Link to="/golvavjamning" >
                    <ListItem button key={"golvavjamning"}>


                        Golvavjmning

                    </ListItem>
                    </Link>
                    <Link to="/vattenskador" >
                    <ListItem button key={"vattenskador"}>


                        Vattenskador

                    </ListItem>
                    </Link>
                    <Link to="/trapprenovering" >
                    <ListItem button key={"trapprenovering"}>


                        Trapprenovering

                    </ListItem>
                    </Link>
                    <Link to="/offertforfragan" >
                    <ListItem button key={"offertforfragan"}>


                        Offertfrfrgan

                    </ListItem>
                    </Link>
                    <Link to="/om-oss" >
                    <ListItem button key={"om-oss"}>


                        Om oss

                    </ListItem>
                    </Link>
                </List>
            </SwipeableDrawer>
        </>
    )
}

export default Nav;